.box{
    margin-top:5%
}
.not
{
    color:red;
}



ol, ul {
    list-style: none;
    margin-left:-14%;
  }


  #autocomplete-container {
    width: 0px;
    margin: 0px auto;
  }
  #autocomplete-input {
    background: grey;
    -moz-appearance: none;
    -webkit-appearance: none;
    width: 100%;
    padding: 20px 15px;
    border: pink;
    outline: none;
    color: black;
    font-size: 20px;
  }
  #autocomplete-input:-webkit-placeholder {
    color: black;
  }
  #autocomplete-input::-moz-placeholder {
    color: black;
  }
  #autocomplete-results {
    display: none;
    width: 100%;
    margin-top: -2px;
    color: black;
  }
  #autocomplete-results li {
    width: 100%;
    padding: 7px 15px;
    border-right: 2px solid #eee;
    border-bottom: 2px solid #eee;
    border-left: 2px solid #eee;
  }
  #autocomplete-results li:hover {
    background:rgba(105, 111, 118, 0.8);;
  }
